<template>
  <div class="confirm-order">
    <header-view :fixed="isfixed" :title="title" :routerName="routerName"></header-view>
    <div class="confirm-order-list">
      <section class="confirm-sp-detail" v-for="(item,key) in spList">
        <header class="my-order-list-storeName"><i></i>{{item.storeName}}</header>
        <div class="hi-life-list-item" v-for="(item1,key1) in item.products">
          <div class="hi-life-list-item-img"><img :src="item1.imageUrl"></div>
          <div class="hi-life-list-item-content">
            <div class="hi-life-list-item-summary">{{item1.summary}}</div>
            <div class="hi-life-list-item-summary"><span v-for="(item,key) in spConfig.config">{{item1.label}}:{{item1.active}}</span></div>
            <div class="hi-life-list-detail"><span class="sp-sales">￥{{item1.sellPrice}}</span><span class="sp-count">x{{item1.amount}}</span></div>
          </div>
        </div>
        <div class="confirm-list">
          <div class="right">共{{item.products|formatsize}}件商品 小计:<span class="yellow">￥{{item.products|formatprice}}</span></div>
        </div>
      </section>
      <section class="pay-type">
        <div class="confirm-list">
          <i @click="checkbox('takeself')" class="checkbox" :class="{'checkbox-selected':pick == 'takeself'}"></i>到店自提
        <span class="right-help">暂未提供送货服务</span>
      </div>
      </section>
      <section class="fufen-oper">
        <div class="confirm-list"><i @click="checkboxFufen" class="checkbox" :class="{'checkbox-selected':fufen}"></i>使用福分
          <small>(1福分可抵扣1元)</small>
          <span class="right-help">可用福分:
            <span class="yellow">{{fuFen}}</span>个</span>
        </div>
        <div class="confirm-list" v-if="fufen">抵扣福分
          <div class="right fufen-shift"><input type="number" v-model="fuFen" placeholder="请填写使用个数"></div>
        </div>
      </section>
    </div>
    <footer><span v-if="fufen">抵扣福分￥</span><span v-if="fufen">{{shiftMoney}}</span>
      <div class="confirm-bottom-right">
        <div class="confirm-bottom-right-mes"><span style="color:#333;">合计:</span><span class="yellow">￥{{spList|formatAll}}</span></div>
        <div class="confirm-bottom-right-btn" @click="submit">提交订单</div>
      </div>
    </footer>
  </div>
</template>

<script>
  import {confirmOrder, getMe} from '../../service/getData'
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        title: '提交订单',
        routerName: '',
        spList: [],
        isfixed: true,
        spConfig: {},
        pick: 'takeself',
        fufen: false,
        fuFen: 0,
        shiftMoney: 0
      }
    },
    created: function () {
      let _this = this
      getMe()
      .then(res => {
        if (res.data.joyScore !== undefined) {
          _this.fuFen = res.data.joyScore
        }
      })
      .catch(() => {
        _this.$toast('请求福分失败')
      })
      this.spList = JSON.parse(this.$route.query.cartList)
    },
    filters: {
      formatsize: function (item) {
        let key = 0,size = 0
        for (key in item) {
          size += item[key].amount
        }
        return size
      },
      formatprice: function (item) {
        let key = 0,price = 0
        for (key in item) {
          price += item[key].amount * item[key].sellPrice
        }
        return price
      },
      formatAll: function (item) {
        let key = 0,count = 0, key1 = 0
        for (key in item ) {
          for (key1 in item[key].products) {
            count += item[key].products[key1].amount * item[key].products[key1].sellPrice
          }
        }
        return count
      }
    },
    methods: {
      checkbox: function (val) {
        if (this.pick === '') {
          this.pick = val
          console.log(this.pick)
        } else {
          this.pick = ''
        }
      },
      checkboxFufen: function () {
        if (this.fufen) {
          this.fufen = false
        } else {
          this.fufen = true
        }
      },
      getRestData: function () {
        /* 得到请求数据 */
       let data = {}, key = 0, key1 = 0, temp,key2 = 0
       data.items = []
       data.deliverWay = this.pick
       if (this.fufen) {
         data.joyScore = this.fuFen
       }
        for (key in this.spList ) {
          temp = {}
          temp.products = []
          temp.storeId = this.spList[key].storeId
          for (key1 in this.spList[key].products) {
            temp.products.push(this.spList[key].products[key1])
          }
          data.items.push(temp)
        }
       console.log(JSON.stringify(data))
       return data
      },
      submit: function () {
        let _this =this
        confirmOrder(this.getRestData())
        .then(res => {
          if (res.data.orderNo !== undefined) {
            _this.$router.push({
              name: 'pay',
              params: {
                money: _this.formatAll,
                shopName: '中国'
              }
            })
          }
        })
        .catch(res => {
          console.log(res)
          _this.$toast('提交失败')
        })
      }
    },
    computed: {
      count: function () {
        var shiftMoney = this.fuFen
        if (shiftMoney >= 1) {
          this.shiftMoney = shiftMoney
          return this.spList.amount * this.spList.price - this.shiftMoney
        } else {
          this.shiftMoney = 0
          return this.spList.amount * this.spList.price
        }
      }
    }
  }
</script>

<style>
/* 提交订单页面 */
.confirm-order{
  height: 100%;
  background-color: #F5F5F5;
}
.pay-type{
  background-color: #FFFFFF;
  padding-left: .1rem;
}
.confirm-order-list{
  height: 100%;
  width: 100%;
  padding: .4rem 0 .75rem 0;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.confirm-sp-detail,.fufen-oper{
  padding-left: .1rem;
  margin-bottom: .05rem;
  background-color: #FFFFFF;
}
.confirm-order .sp-sales{
  color: rgb(255, 161, 38);
}
.confirm-order .hi-life-list-item-summary:first-child{
  color: #333;
}
.confirm-order .hi-life-list-item-summary span{
  margin-left: .05rem;
}
.confirm-list{
  clear: both;
  height: .45rem;
  line-height: .45rem;
  color: #333;
  padding-right: .15rem;
    border-bottom: .01rem solid #E5E5E5;
}
.confirm-sp-detail .confirm-list:last-child,.fufen-oper .confirm-list:last-child{
  border-bottom: none;
}
.confirm-order>footer{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: .45rem;
  padding-left: .15rem;
  line-height: .45rem;
   border-top: .01rem solid #E5E5E5;
  background-color: #FFFFFF;
}
.confirm-bottom-right{
  float: right;
  display: flex;
  width: 2.1rem;
  height: 100%;
}
.confirm-bottom-right-mes{
  width: 50%;
  vertical-align: top;
  text-align: center;
  height: 100%;
}
.confirm-bottom-right-btn{
  display: inline-block;
  vertical-align: top;
  text-align: center;
  height: 100%;
  width: 50%;
  color: #FFFFFF;
    background: -webkit-linear-gradient(left, rgb(255, 161, 38), rgb(255, 109, 24));
    background: linear-gradient(to right, rgb(255, 161, 38), rgb(255, 109, 24));
}
.checkbox{
  display: inline-block;
  vertical-align: middle;
  margin-right: .1rem;
  width: .2rem;
  height: .2rem;
  background: url(/static/image/order_dis_select@3x.png);
  background-size: 100% 100%;
}
.checkbox-selected{
  background: url(/static/image/shopping_cart_list_select@3x.png);
  background-size: 100% 100%;
}
.right-help{
  float: right;
  font-size: .12rem;
  color: #999999;
}
.right{
  float: right;
  font-size: .12rem;
}
.yellow{
  color: rgb(255, 161, 38);
}
.fufen-shift input{
  width: 1rem;
  height: 100%;
  text-align: right;
}
.hi-life-list-detail span.sp-count {
  float: right;
} 
</style>


